.file-input {
  display: flex;

  &__label {
    cursor: pointer;
    flex: 1 1 auto;
    display: flex;
    margin-right: 0.5rem;
  }

  & input[type='file'] {
    display: none;
  }

  & input[type='text'] {
    flex: 1 1 auto;
    margin-right: 0.5rem;
    pointer-events: none;
  }

  &__btn {
    pointer-events: none;
    background-color: #fbfbfb;
    border: 1px dashed #d9d9d9;
    border-radius: 5px;
    color: #357ce1;
    padding: 20px 18px;
    box-sizing: border-box;

    &--active {
      color: rgb(18, 164, 18)!important;

      svg {
        fill: rgb(18, 164, 18)!important;
      }
    }

    svg {
      fill: #357ce1;
    }
  }
}
