.select-modal {
  width: 100%;
  max-width: 420px;
  margin: auto;

  &__header {
    display: flex;
    width: 100%;
    justify-content: space-between;
  }

  &__content {
    width: 100%;
  }

  &__cross-icon {
    width: 18px;
  }

  &__list {
    margin-top: 10px;
  }

  &__option {
    padding: 10px;
    cursor: pointer;

    &:hover {
      background-color: rgb(245, 245, 245);
    }
  }
}
