@import 'styles/colors';

.navbar-modal {
  justify-content: flex-end;
  min-height: 100%;
  padding: 0;
  box-sizing: border-box;

  &__nav-list {
    display: flex;
    flex-direction: column;
  }

  &__nav-link {
    font-size: 18px;
    padding: 16px 0;
    cursor: pointer;
    color: $White;
  }

  &__btn {
    color: $White;
  }

  &__header {
    display: flex;
    justify-content: flex-end;
  }

  &__hamburger {
    display: flex;
    justify-content: flex-end;
  }

  &__content {
    width: 100%;
    max-width: 376px;
    background-color: $Blue;
    padding: 8px 24px;
  }
}
