@import 'styles/colors';

.message-item {
  background: #f5f5f5;
  border-radius: 5px;
  padding: 0.5rem;
  border: 1px solid #ebedf0;
  margin-bottom: 1rem;

  @media (min-width: 768px) {
    flex: 0 0 50%;
    max-width: 50%;
  }

  &__header {
    display: flex;
    justify-content: space-between;

    &-username {
      font-weight: normal;
      font-size: 12px;

      &-light {
        color: $Black80;
        padding-left: 4px;
      }
    }

    &-date {
      padding: 0.15rem 0.5rem;
      background: rgba(100,100,100,.1);
      border-radius: 5px;
      font-size: 11px;
    }
  }

  &__body {
    padding: 0.5rem;
  }
}