@import 'styles/colors';

.deposit {
  padding: 18px;

  &__form-group {
    margin-bottom: 1rem !important;
  }

  &__label {
    display: block;
    width: 100%;
    box-sizing: border-box;
    padding: 0.25rem;
    border: none;
    border-radius: 3px;
    outline: none;
    text-align: left;
    margin-bottom: 0.5rem;
  }

  &__input {
    line-height: 22px;
    font-size: 15px;
  }

  &__header {
    font-size: 1.25rem;
    color: #3f475a;
    margin: 0 0 10px;
  }

  &__form {
    width: 100%;
    max-width: 540px;
  }

  &__btn-submit {
    line-height: 22px;
    border-radius: 2px;
    color: $Blue;
    background-color: $Yellow;
    border: none;
    -webkit-box-shadow: none;
    box-shadow: none;
    outline: 0;
    font-weight: 600;
    box-sizing: border-box;
    margin-top: 5px;

    &:not(:disabled):hover {
      background-color: $Yellow;
    }
  }

  &__warn {
    display: flex;
    align-items: center;
    margin: -6px 0 15px 0;
    background: rgb(254, 246, 216);
    border: 1px solid rgb(254, 246, 216);
    line-height: 1.2;
    color: rgb(30, 35, 41);
    padding: 12px 15px;
    font-size: 13px;
    text-align: center;

    &-text {
      padding: 0 2px;
      line-height: 1.25rem;
    }
    &-logo {
      width: 18px;

      fill: rgb(240, 185, 11);
      padding: 0 0 18px 5px;
    }
  }
}
