@import 'styles/colors';

$BorderColor: #ebedf0;
$Color: #3f475a;

.table {
  width: 100%;
  border-width: 1px;
  border-style: solid;
  border-color: $BorderColor;
  border-radius: 0.25rem;
  border-collapse: collapse;
  border-top: none;
  background-color: $White;

  &--fixed {
    table-layout: fixed;
  }

  &__caption {
    padding: 1rem 1.25rem;
    font-size: 1.25rem;
    color: $Color;
    text-align: left;
    border: 1px solid $BorderColor;
    border-bottom: 0;
  }

  &__head {
    background-color: $White;
    border-bottom: 1px solid $BorderColor;
    color: rgba(10, 15, 56, 0.6);

    &-cell {
      color: $Color;
      padding: 1.25rem;
      font-weight: bold;
      font-size: 0.875rem;
      line-height: 1.5rem;
      text-align: left;

      &--orderable:hover {
        cursor: pointer;
      }
    }
  }

  &__body & {
    &__row {
      &:hover {
        //background-color: $Black;
      }

      &.head {
        background-color: $White;

        &:hover {
          background-color: $Black80;
        }
      }
    }
  }

  &__row {
    border-top: 1px solid $BorderColor;
    border-bottom: 1px solid $BorderColor;

    &--clickable {
      cursor: pointer;
    }

    &:first-child {
      border-top: 0;
    }

    &:last-child {
      border-bottom: 0;
    }
  }

  &__cell {
    padding: 1.25rem;
    font-size: 0.875rem;
    vertical-align: top;
    text-align: left;
  }
}

.settings-icon {
  width: 1.125rem;
  min-width: 1.125rem;
  height: 1.125rem;
  cursor: pointer;
}

.copy-icon {
  margin-top: 2px;
  margin-left: 2px;
  cursor: pointer;
}

.arrow {
  width: 1.5rem;
  height: 1.5rem;
  opacity: 0;
  transition: transform 0.3s, opacity 0.3s;

  &--dir-up {
    opacity: 1;
    transform: rotate(180deg);
  }

  &--dir-down {
    opacity: 1;
  }
}
