@import 'styles/colors';

.support {
  margin-bottom: 100px;
  border: 1px solid #ebedf0;
  padding: 0.5rem;

  &__header {
    display: flex;
    align-items: center;
    line-height: 1.8;
    color: rgb(30, 35, 41);
    border-bottom: 1px solid #ebedf0;
    word-break: break-word;
    padding: 8px 10px 14px;
    background: #fafafa;
    margin-bottom: 5px;

    &-bold {
      font-weight: 700;
      padding: 0 8px 0 3px;
    }

    svg {
      fill: #0cb00c;
    }
  }

  &__chat-box {
    padding: 1rem;
  }

  &__message-list {
    height: 480px;
    overflow-y: auto;
    padding: 1rem 1rem 0 1rem;
    margin-bottom: 1rem;
  }

  &__form {
    display: flex;
    flex-direction: row;
  }

  &__btn-submit {
    width: 90px;
    line-height: 22px;
    border-radius: 2px;
    color: $Blue;
    background-color: $Yellow;
    border: none;
    -webkit-box-shadow: none;
    box-shadow: none;
    outline: 0;
    font-weight: 600;
    box-sizing: border-box;
    margin-left: 4px;

    &:not(:disabled):hover {
      background-color: $Yellow;
    }
  }
}