@import 'styles/colors';

.verification {
  text-align: center;
  color: #3f475a;
  border: 1px solid rgb(216, 220, 230);
  border-radius: 2px;
  padding-bottom: 30px;

  svg {
    width: 14px;
    height: 14px;
  }

  &__header {
    line-height: 60px;
    font-weight: bold;
    font-size: 20px;
  }

  &__sub-header {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 60px;
    font-size: 18px;

    svg {
      fill: #357ce1;
      margin-right: 6px;
    }

    &-text {
      margin-left: 4px;
    }
  }

  &__form {
    max-width: 340px;
    margin: auto;
  }

  &__label {
    margin-bottom: 5px;
  }

  &__input {
    border: 1px solid #d8dce6;
    background-color: $White;
  }

  &__form-group {
    margin-bottom: 24px!important;
  }

  &__btn-submit {
    line-height: 22px;
    border-radius: 2px;
    color: $Blue;
    background-color: $Yellow;
    border: none;
    -webkit-box-shadow: none;
    box-shadow: none;
    outline: 0;
    font-weight: 600;
    box-sizing: border-box;
    margin-top: 5px;

    &:not(:disabled):hover {
      background-color: $Yellow;
    }
  }
}